'use client';

import { useState, useEffect } from 'react';
import { useCart } from '@/lib/cart/CartContext';
import { QRCodeCanvas } from 'qrcode.react';
import Image from 'next/image';

interface CryptoOption {
  value: CurrencyType;
  label: string;
  icon: string;
}

type CurrencyType = 'BTC' | 'LTC' | 'TRON' | 'MONERO' | 'ZCASH' | 'USDT';

const cryptoOptions: CryptoOption[] = [
  { value: 'BTC', label: 'Bitcoin', icon: '/crypto-icons/bitcoin-btc-logo.svg' },
  { value: 'LTC', label: 'Litecoin', icon: '/crypto-icons/litecoin-ltc-logo.svg' },
  { value: 'TRON', label: 'Tron', icon: '/crypto-icons/tron-trx-logo.svg' },
  { value: 'MONERO', label: 'Monero', icon: '/crypto-icons/monero-xmr-logo.svg' },
  { value: 'ZCASH', label: 'Zcash', icon: '/crypto-icons/zcash-zec-logo.svg' },
  { value: 'USDT', label: 'USDT', icon: '/crypto-icons/usd-coin-usdc-logo.svg' }
];

const CheckoutPage = () => {
  const { items, total, clearCart } = useCart();
  const [email, setEmail] = useState('');
  const [selectedCrypto, setSelectedCrypto] = useState<CryptoOption | null>(null);
  const [paymentStatus, setPaymentStatus] = useState<'pending' | 'processing' | 'completed' | 'failed'>('pending');
  const [qrData, setQrData] = useState('');
  const [orderId, setOrderId] = useState<string | null>(null);
  const [walletAddress, setWalletAddress] = useState('');
  const [downloadLink, setDownloadLink] = useState('');

  const handlePayment = async () => {
    if (!email || !selectedCrypto) return;

    try {
      setPaymentStatus('processing');
      const response = await fetch('http://localhost:5000/payment/init', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          items,
          email,
          currency: selectedCrypto.value,
          amount: total
        })
      });

      const data = await response.json();
      if (!response.ok) throw new Error(data.error);

      setQrData(data.qrCode);
      setOrderId(data.orderId);
      setWalletAddress(data.walletAddress);
      startPaymentCheck(data.orderId);
    } catch (error) {
      console.error('Payment initialization failed:', error);
      setPaymentStatus('failed');
    }
  };

  const startPaymentCheck = (orderId: string) => {
    const interval = setInterval(async () => {
      try {
        const response = await fetch(`http://localhost:5000/payment/status/${orderId}`);
        const data = await response.json();
        
        if (data.status === 'completed') {
          setPaymentStatus('completed');
          setDownloadLink(data.downloadLink);
          clearCart();
          clearInterval(interval);
        }
      } catch (error) {
        console.error('Payment status check failed:', error);
      }
    }, 5000);
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="max-w-4xl mx-auto bg-white rounded-lg shadow-lg overflow-hidden">
        <div className="md:flex">
          {/* Left Panel - Cart Summary */}
          <div className="md:w-1/2 p-8 bg-gray-50">
            <h2 className="text-2xl font-bold mb-6">Order Summary</h2>
            <div className="space-y-4">
              {items.map(item => (
                <div key={`${item.id}-${item.format}`} className="flex justify-between items-center border-b pb-4">
                  <div>
                    <h3 className="font-medium">{item.title}</h3>
                    <p className="text-sm text-gray-600">Format: {item.format}</p>
                  </div>
                  <span className="font-semibold">${item.price}</span>
                </div>
              ))}
              <div className="flex justify-between items-center pt-4">
                <span className="text-xl font-bold">Total</span>
                <span className="text-xl font-bold">${total}</span>
              </div>
            </div>
          </div>

          {/* Right Panel - Payment */}
          <div className="md:w-1/2 p-8 border-l">
            <h2 className="text-2xl font-bold mb-6">Payment Details</h2>
            
            {/* Email Input */}
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Email Address
              </label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full p-3 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder="your@email.com"
              />
            </div>

            {/* Crypto Selection */}
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Select Payment Method
              </label>
              <div className="grid grid-cols-3 gap-4">
                {cryptoOptions.map(option => (
                  <button
                    key={option.value}
                    onClick={() => setSelectedCrypto(option)}
                    className={`p-4 border rounded-lg flex flex-col items-center ${
                      selectedCrypto?.value === option.value
                        ? 'border-blue-500 ring-2 ring-blue-500'
                        : 'hover:border-gray-300'
                    }`}
                  >
                    <Image
                      src={option.icon}
                      alt={option.label}
                      width={32}
                      height={32}
                    />
                    <span className="text-sm mt-2">{option.label}</span>
                  </button>
                ))}
              </div>
            </div>

            {/* Payment Status and QR Code */}
            {qrData && (
              <div className="text-center p-6 border rounded-lg bg-gray-50">
                <QRCodeCanvas
                  value={qrData}
                  size={200}
                  level="H"
                  className="mx-auto mb-4"
                />
                <div className="text-sm text-gray-600 break-all">
                  <p className="mb-2">Wallet Address:</p>
                  <p className="font-mono bg-white p-2 rounded">{walletAddress}</p>
                </div>
                <div className="mt-4">
                  <p className="text-sm text-gray-600">
                    Status: {paymentStatus.charAt(0).toUpperCase() + paymentStatus.slice(1)}
                  </p>
                </div>
              </div>
            )}

            {/* Download Link */}
            {downloadLink && (
              <div className="mt-6 p-4 bg-green-50 border border-green-200 rounded-lg">
                <p className="text-green-800 mb-2">Payment Successful!</p>
                <a
                  href={downloadLink}
                  className="text-blue-600 hover:text-blue-800 underline"
                  download
                >
                  Download your ebook
                </a>
              </div>
            )}

            {/* Pay Button */}
            <button
              onClick={handlePayment}
              disabled={!email || !selectedCrypto || paymentStatus !== 'pending'}
              className="w-full mt-6 bg-blue-600 text-white py-3 px-6 rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed"
            >
              {paymentStatus === 'pending' ? 'Pay Now' : 'Processing Payment...'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CheckoutPage;